<template>
  <NavbarItem />
  <div class="cart">
    <h2>Your Cart</h2>
    <div v-if="cartItems.length === 0">
      <p>Your cart is empty.</p>
      <div>
        <a href="/">Continue shopping</a>
      </div>
    </div>
    <div v-else>
      <table>
        <thead>
          <tr>
            <th>Image</th> <!-- Ct nh -->
            <th>Product</th>
            <th>Quantity</th>
            <th>Price</th>
            <th>Total</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in cartItems" :key="index">
            <td>
              <img :src="item.image" alt="Product Image" class="product-image" /> <!-- nh sn phm -->
            </td>
            <td>{{ item.name }}</td>
            <td>
              <input 
                type="number" 
                v-model="item.quantity" 
                :min="1" 
                @change="updateCartTotal"
              />
            </td>
            <td>${{ item.price }}</td>
            <td>${{ item.price * item.quantity }}</td>
            <td>
              <button @click="removeFromCart(index)">Remove</button>
            </td>
          </tr>
        </tbody>
      </table>
      <div>
        <a href="/">Continue shopping</a>
      </div>
      <div class="cart-summary">
        <p>Total: ${{ cartTotal }}</p>
        <button @click="initiatePayment">Thanh ton</button>
      </div>
    </div>
  </div>

</template>

<script>
import axios from 'axios';
import NavbarItem from './NavbarItem.vue';
import Footer from './footer.vue';
export default {
  name: 'CartComponent',
  components: {
    NavbarItem,
    Footer
  },
  data() {
    return {
      cartItems: []
    };
  },
  computed: {
    cartTotal() {
      return this.cartItems.reduce((total, item) => total + (item.price * item.quantity), 0);
    }
  },
  methods: {
    loadCart() {
      const cart = localStorage.getItem('cart');
      if (cart) {
        try {
          const parsedCart = JSON.parse(cart);
          if (Array.isArray(parsedCart)) {
            this.cartItems = parsedCart;
          } else {
            this.cartItems = [];
          }
        } catch (error) {
          console.error('Error parsing cart data:', error);
          this.cartItems = [];
        }
      }
    },
    async initiatePayment() {
      try {
        const token = localStorage.getItem('token');
        if (!token) {
          alert('Bn cn ng nhp  thc hin thanh ton');
          window.location.href = '/login';  // Hoc URL ca trang ng nhp ca bn
          return; // Dng li, khng gi API na
        }

        const totalAmount = this.cartTotal * 100; // V tng tin ca bn ang  dng USD, bn cn nhn vi 100  chuyn sang VND.

        const response = await axios.get('http://localhost:8388/payment/create_payment', {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`,
          },
          params: {
            amount: totalAmount,  // Truyn tng tin vo API
          }
        });

        // D liu tr v t API
        const data = response.data;

        if (data.status === 'ok') {
          // Lu URL thanh ton
          this.paymentUrl = data.url;
          // M thanh ton trong tab mi
          window.open(this.paymentUrl, '_blank');

          this.cartItems = [];
          this.saveCart();  // Lu li gi hng  c lm sch

          // Hin th thng bo thnh cng
          alert('Thanh ton thnh cng! Cm n bn  mua hng.');
        } else {
          // X l khi API tr v trng thi khc "ok"
          alert('C li xy ra trong qu trnh thanh ton');
        }
      } catch (error) {
        // X l li nu API call tht bi
        console.error('Li khi gi API thanh ton:', error);
        alert('C li xy ra, vui lng th li');
      }
    },


    saveCart() {
      localStorage.setItem('cart', JSON.stringify(this.cartItems));
    },
    removeFromCart(index) {
      this.cartItems.splice(index, 1);
      this.saveCart();
    },
    updateCartTotal() {
      this.saveCart();
    },
    proceedToCheckout() {
      // X l chuyn hng n trang thanh ton hoc gi hng.
      alert("Proceeding to checkout...");
    }
  },
  created() {
    this.loadCart();
  },
  watch: {
    cartItems: {
      handler() {
        this.saveCart();
      },
      deep: true
    }
  }
};
</script>

<style scoped>
.cart {
  text-align: left;
  margin: 20px;
}
table {
  width: 100%;
  border-collapse: collapse;
}
table th, table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: center;
}
input[type="number"] {
  width: 60px;
  padding: 5px;
  text-align: center;
}
button {
  background-color: #ff0000;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
}
button:hover {
  background-color: #cc0000;
}
.cart-summary {
  margin-top: 20px;
  text-align: right;
}
.cart-summary button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
}
.cart-summary button:hover {
  background-color: #45a049;
}

/* Style cho nh sn phm */
.product-image {
  width: 50px; /* iu chnh kch thc nh */
  height: 50px; /* iu chnh kch thc nh */
  object-fit: cover; /* Gi t l nh */
  border-radius: 5px; /* To gc bo trn cho nh */
}
</style>